* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav, main {
  font-size: calc(1.2vw + 50%);
}

nav {
  display: grid;
  align-items: center;
  padding: 2vw 1vw 2vw 3vw;
  height: 100vh;
}

nav header{
  align-self: flex-end;
}

main {
  text-align: center;
  display: grid;
  align-items: center;
}

@media screen and (max-width: 900px){
  nav{
    padding: 4vw 20vw 4vw 20vw;
    height: 30vh;
  }

  nav h1{
    text-align: center;
  }

  .Logo{
    text-align: center;
    font-size: 10vw;
  }

  nav ul{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  nav li{
    text-align: center;
  }

  main{
    padding: 0;
    height: 70vh;
  }
}

footer {
  padding: 2vw;
  display: grid;
  justify-content: center;
  background: #EEEEEE;
  font-size: small;
  text-align: center;
}

ul{
  list-style-type: none;
}

li{
  padding: 1vw 0;
}

a {
  text-decoration: none;
  color: #fff;
}

h1, h2, h3, h5, h6{
  margin-block-start: 0 !important;
  margin-block-end: 0 !important;
}

p {
  font-size: calc(0.2vw + 40%);
}

.active {
  text-decoration: underline #fff;
}

.App-header {
  font-size: calc(3vw + 40%);
  color: white;
}

.Header {
  color: #fff;
  padding: 0 0 2vw 0;
  font-size: calc(1vw + 50%);
}

@media screen and (max-width: 900px){
  h1{
    font-size: calc(3vw + 50%);
  }
  p{
    font-size: calc(0.5vw + 40%);
  }
  
}

.Grid-container{
  display: grid;
}

.Grid-two-thirds{
  grid-template-columns: 1fr 2.5fr;
}

.App {
  /* min-height: 100vh; */
  background: #FEAC5E;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #4BC0C8, #C779D0, #FEAC5E);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4BC0C8, #C779D0, #FEAC5E); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

@media screen and (max-width: 900px){
  .Grid-two-thirds{
    display: block;
    /* height: 100vh; */
    margin: 0;
  }

  .App{
    height: 100vh;
  }
}

.Clear{
  background: #22c1c3;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #fdbb2d, #56CCF2);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fdbb2d, #56CCF2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.Night{
  background: #373B44;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #4286f4, #373B44);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4286f4, #373B44); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.Clouds{
  background: #D3CCE3;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #E9E4F0, #D3CCE3);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #E9E4F0, #D3CCE3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
} 
.Drizzle, .Rain{
  background: #7F7FD5;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #91EAE4, #86A8E7, #7F7FD5);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #91EAE4, #86A8E7, #7F7FD5); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.Snow{
  background: #8e9eab;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #eef2f3, #8e9eab);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #eef2f3, #8e9eab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

